import React from 'react'
import s from '../Components/Catalog/Card/styles.module.scss'
import d from '../Components/Catalog/styles.module.scss'

export default function Template({data}) {
  const {markdownRemark: card} = data;
  return (
    <div>
      <div className={d.container}>
        <h3 className={d.title}>Каталог iPhone</h3>
        <div className={d.catalogWrapper}>
            {data.allMarkdownRemark.edges.map(card => (
                <article className={s.productCard} key={card.node.id}>
                  <div className={s.productCard__photoContainer}>
                      <img src={card.node.frontmatter.photo} alt=''/>
                  </div>
                  <div className={s.productCard__container}>
                      <a className={s.productCard__link}>{card.node.frontmatter.title}</a>
                      <span className={s.productCard__price}>{card.node.frontmatter.price}</span>
                      <button>Добавить в корзину</button>
                  </div>
                </article>
            ))}
        </div>
      </div>
    </div>
  )
}

export const cardQuery = graphql`
  query CardByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        path
        title
        price
        photo
      }
    },
    allMarkdownRemark(limit: 1000) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            price
            photo
          }
        }
      }
    }
  }
`




